@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Source+Sans+Pro:wght@300&family=Work+Sans:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noticia+Text&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Serif+JP&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0}body{background-color:#1f1f1f}.header_bg{object-fit:cover;width:100%;height:400px}.header_bar{position:relative;height:3rem;margin-top:-3.5rem;padding-top:2rem;padding-bottom:0.5rem;background:linear-gradient(rgba(0,0,0,0), #1f1f1f 3rem)}.newspaper{position:relative;padding:1rem;max-width:90%;min-width:80%;background-color:#bbaeb2;box-shadow:0.5rem -0.5rem 0.3rem 0 rgba(0,0,0,0.75);text-align:center;margin-top:-1.5rem;font-size:1rem;display:grid;grid-template-columns:1fr -webkit-max-content 1fr;grid-template-columns:1fr max-content 1fr;grid-template-rows:auto;grid-template-areas:"lineT lineT lineT"   ". title date" "lineB lineB lineB" "main main memories" "happy happy memories" "love love memories"}@media screen and (min-width: 700px){.newspaper{font-size:2vw}}.newspaper__icon{justify-self:start;align-self:center;margin-left:1rem}.newspaper__icon svg{height:3.5rem;width:3rem}.newspaper__title{grid-area:title}.newspaper__title h1{font-family:'Notica Text', serif;font-size:1.8em;color:#2E2D27}.newspaper__title h2{color:#2E2D27;font-family:'Notica Text', serif;font-size:1.2em}.newspaper__date{grid-area:date;font-size:0.9em;justify-self:end;align-self:center;padding-right:1rem}.newspaper__line{background-color:#2E2D27;height:3px}.newspaper__line--t{grid-area:lineT}.newspaper__line--b{grid-area:lineB}.article{font-size:1rem;margin:1rem 2rem;color:#2E2D27}.article h1{text-align:left;margin-bottom:0.5rem}.article p{font-size:1.1em;text-align:left;margin-bottom:0.5rem;margin-left:1rem;font-family:'Noto Serif JP', serif}.main-article{grid-area:main}.memories{grid-area:memories;font-size:1rem;margin:1rem 0.5rem;text-align:left}.memories ul{padding-left:1rem}.memories li{padding:0.25rem}.memories h1{text-align:left}.memories h2{margin-left:-1rem;margin-top:0.5rem}.thought{object-fit:contain;width:12rem;height:12rem;float:right;margin-right:1rem;margin-left:2rem}.happy{grid-area:happy;margin:1rem;text-align:left}.love{font-size:1rem;grid-area:love;margin:1rem;text-align:left}.centered_rel{display:flex;justify-content:center}

